0 .75rem 0;\n  color:#111; background:#fff;\n}\n.input::placeholder, .textarea::placeholder { color:#666; }\n.label{font-size:.9rem;color:#333}\n.helper{font-size:.8rem;color:#666;margin-top:-.5rem;margin-bottom:.5rem}\n.error{color:#b00020;font-size:.85rem;margin-top:-.4rem;margin-bottom:.6rem}\n.grid{display:grid;grid-template-columns:repeat(auto-fill,minmax(260px,1fr));gap:1rem}\n.flex{display:flex;gap:.75rem;align-items:center;flex-wrap:wrap}\n.badge{background:var(--merge-light-blue);color:#000;border-radius:999px;padding:.2rem .5rem;font-size:.8rem}\nhr{border:0;border-top:1px solid #eee;margin:1rem 0}\nh1,h2,h3{color:#111;margin:.2rem 0 .6rem}\nsmall.muted{color:#666}\na{color:var(--merge-indigo)}\nform{max-width:480px}\n\n\n\n.splash{\n  position:relative; min-height:100vh; overflow:hidden;\n  /* soft gradient within palette */\n  background: radial-gradient(1200px 800px at 10% 10%, #ef95f4 0%, #7c7cc1 40%, #000000 100%);\n  color: var(--merge-white);\n}\n.splash-top{\n  display:flex; align-items:center; justify-content:space-between;\n  padding: 1rem 1.25rem;\n}\n.splash-brand img{ height:40px; width:auto; display:block; }\n.visually-hidden{ position:absolute; left:-9999px; }\n\n.top-actions{ display:flex; gap:.6rem; }\n.pill{\n  display:inline-flex; align-items:center; justify-content:center;\n  padding:.5rem .9rem; border-radius:999px; text-decoration:none;\n  font-weight:600; border:1px solid rgba(255,255,255,.25);\n}\n.pill-ghost{ font-family: 'Montserrat';background:transparent; color:#fff; }\n.pill-gradient{\n  background: linear-gradient(90deg, var(--merge-light-blue), var(--merge-pink));\n  color:#1f1162; border-color: transparent;\n}\n\n.splash-hero{\n  display:grid; grid-template-columns: 1.1fr 1fr; gap:2rem;\n  align-items:center; padding-top:2rem; padding-bottom:3rem;\n}\n@media (max-width: 900px){\n  .splash-hero{ grid-template-columns: 1fr; padding-top:1rem; }\n}\n\n.splash-copy .lede{\n  font-family: 'Montserrat';\n  font-size:1.15rem; line-height:1.6; margin:.6rem 0; color:#fff;\n}\n.splash-meta{ margin-top:1.25rem; opacity:.9; }\n.splash-meta strong{ display:block; font-size:1.25rem; color:#cfd8ff; }\n.splash-meta span{ color:#bfc7ff; }\n\n.hero-title{\n  font-family: 'Goldman';\n  font-size: clamp(2rem, 6vw, 4rem);\n  line-height:1.05; margin:0 0 1.25rem; color:#cfe4ff;\n  letter-spacing:.3px;\n}\n\n.cta-row{ margin-top:.75rem; }\n.cta-large{\n  display:inline-block; font-weight:700; text-decoration:none;\n  padding: .9rem 1.25rem; border-radius:999px;\n  background: linear-gradient(90deg, var(--merge-light-blue), var(--merge-pink));\n  color:#000; box-shadow: 0 6px 22px rgba(0,0,0,.25);\n  border:1px solid rgba(255,255,255,.15);\n}\n\n.splash-pattern{\n  position:absolute; inset:0; background-repeat:no-repeat; background-size:cover;\n  opacity:.12; pointer-events:none;\n}\n.splash-arrow{\n  position:absolute; right:14vw; top:62vh; width:120px; opacity:.8; pointer-events:none;\n}\n\n.splash { position: relative; }\n.splash-pattern {\n  position: absolute; inset: 0;\n  background-repeat: no-repeat; background-size: cover;\n  opacity: .12; pointer-events: none; z-index: 0;\n}\n.splash-top, .splash-hero { position: relative; z-index: 1; }\n\n/* Ensure forms in splash cards use dark text on light backgrounds */\n.splash .card { color:#111; }\n.splash .card .label { color:#333; }\n.splash .card .input, .splash .card .select, .splash .card .textarea { color:#111; background:#fff; }\n"],"sourceRoot":""}]);
