import React from "react";
import { Link, NavLink } from "react-router-dom";

export default function Header({ user, onLogout }) {
  return (
    <nav className="navbar" role="navigation" aria-label="Main">
      <Link className="brand" to={user ? "/home" : "/"}>
        <img 
            src="/assets/images/mergelogo.png" 
            alt="Merge logo" 
            style={{ height: "40px" }} 
          />
      </Link>


      <div className="navlinks">
        <NavLink to="/home" className="navlink"> Home </NavLink>

        {user && (
          <>
            <span className="navlink navlink-static">Hi, {user.username || user.name}</span>
            <NavLink to={`/profile/${user._id}`} className="navlink">
              Profile
            </NavLink>
          </>
        )}

        {user ? (
          <button className="navlink" type="button" onClick={onLogout}>Log out</button>
        ) : (
          <>
            <NavLink to="/login" className="navlink"> Log in </NavLink>
            <NavLink to="/signup" className="navlink"> Sign up </NavLink>
          </>
        )}
        {user && (
          <NavLink to="/search" className="navlink"> Search </NavLink>
        )}
      </div>
    </nav>
  );
}
